 .cartItem {
    width: 700px;
    height: 250px;
    display: flex;
  
    align-items: center;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
    border-radius: 25px;
    margin: 30px;
}
  
.cartItem img {
    width: 200px;
}
  
.cartItem .description {
    width: 100%;
    font-size: 30px;
}
  
.countHandler input {
    width: 40px;
    text-align: center;
    font-weight: bolder;
}